.contacts {
  &__inner {
    // position: absolute;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    // margin:0 15px;
    transition: transform 0.6s;
    width: 100%;

    @include mq("1400") {
      flex-wrap: wrap;
    }

    @include mq("1100") {
      // position: unset;
      // top: unset;
      // transform: unset;
    }
  }

  &__body {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;

    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    flex: 1 1 0%;
    padding: 1em;
    margin-bottom: 20px;

    @include mq("1400") {
      justify-content: center;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
    text-align: center;
    font-size: 24px;
    color: #073b4c;
  }

  &__content {
    margin: 0 10px 30px;
    // border-radius: 6px;
    padding: 10px;
    min-width: 280px;
    // max-width: 65%;
    font-size: 15px;
    color: #000;
    // background: rgba(0, 0, 0, 0.05);
    backdrop-filter: blur(7.2px);
    // border: 1px solid rgba(0, 0, 0, 0.09);
    user-select: none;

    @include mq("1100") {
      max-width: 100%;
      background: transparent;
      border: none;
      padding: 0;
      backdrop-filter: unset;
    }
  }

  &__text {
    margin: 0 10px;
  }

  &__top-title {
    margin-bottom: 20px;
    font-size: 18px;
    color: #073b4c;
    // color: #fff;
    // text-shadow: 1px 1px 1px #000;
    text-align: center;
  }

  &__middle-title {
    margin-bottom: 10px;
    font-size: 24px;
    color: $title;

    white-space: nowrap;
    overflow: hidden;
    border-right: 2px solid $title;

    animation: cursor 1s infinite step-end, typing-middle 5s infinite steps(16);

    &--bottom {
      margin-bottom: 10px;
      font-size: 24px;
      color: $title;
    }
  }

  @keyframes cursor {

    0%,
    100% {
      border-color: transparent;
    }

    50% {
      border-color: $title;
    }
  }

  @keyframes typing-middle {
    0% {
      width: 0ch;
    }

    50% {
      width: 8ch;
    }

    80% {
      width: 8ch;
    }

    100% {
      width: 0ch;
    }
  }

  &__middle-list {
    list-style: inside !important;
    margin-bottom: 15px;

    li {
      list-style: none;
      margin-bottom: 6px;
      font-size: 18px;
      // color: #fff;
      // text-shadow: 1px 1px 1px #000;

      padding: 0.25em;
      margin-bottom: 0.25em;

      &:not(:last-child) {
        border-bottom: 1px solid $title;
      }
    }
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;

    // @include mq("1100") {
    //   margin-bottom: 30px;
    // }
  }

  &__item {
    transition: transform 0.4s;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 15px;
    }

    &:hover {
      transform: scale(1.08);
    }
  }

  &__link {
    font-size: 1rem;
    color: #ececec;
  }

  &__icon {
    max-width: 50px;
    max-height: 50px;

    @include mq("600") {
      width: 40px;
    }
  }
}